@import url("https://fonts.googleapis.com/css2?family=Jost:wght@500&display=swap");
@import url('https://fonts.googleapis.com/css2?family=EB+Garamond:wght@500&family=Krub:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lora&display=swap');

@font-face {
  font-family: "Gucci";
  src: url(../../Font/futura-font/futura\ light\ bt.ttf) format("truetype");
}

:root {
  --clr-black: rgb(0, 0, 0);
  --clr-white: rgb(255, 255, 255);
  --clr-lightGray: #e7e7e7;
  --clr-gold: rgb(159, 110, 2);
  --clr-red: rgb(213, 17, 17);
  --clr-fgreen: rgb(8, 88, 32);
}

p {
  margin-bottom: 0px;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Gucci';
}

.hero-products-checkout {
  width: 100%;
  height: 400px;
  object-fit: cover;
  background: rgba(30, 28, 28, 0.271);
  background-image: url("../../images/herosummer.avif");
  background-size: cover;
  background-blend-mode: soft-light;
}

section {
  margin: 20px;
  background: var(--clr-lightGray);
}

.container-container {
  text-align: left;
  position: relative;
  padding: 20px;
  display: flex;
  justify-content: space-around;
  width: 80%;
  margin: 0 auto;
}

.bag-content {
  text-align: left;
  background: var(--clr-lightGray);
  position: relative;
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 35px;
}

.bag-title-container {
  color: #999;
  letter-spacing: 2px;
  font-style: normal;
  display: block;
  font-size: 14px;
  text-transform: uppercase;
  margin: 20px 0px 0px 20px;
  position: absolute;
  left: 8.6%;
}

.shopping-bag-img {
  width: 200px;
  height: 240px;
  margin-right: 30px;
  aspect-ratio: 3/2;
  object-fit: contain;
  mix-blend-mode: darken;
}

.shopping-bag-content {
  background-color: var(--clr-lightGray);
  width: 100%;
  margin: 0 auto;
}

.salvador {
  display: flex;
  flex-direction: column;
  width: 60%;
}

.shopping-bag-content .order-summary-title {
  color: #999;
  padding: 28px 0 15px 0;
  line-height: initial;
  font-size: 15px;
  display: block;
  margin-top: 5px;
  padding-top: 5px;
  text-transform: uppercase;
}

.hr-before-wr {
  width: 100%;
  height: 2px;
  margin-left: 60px;
}

.shopping-bag-wrapper {
  width: 100%;
  height: 180px;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  margin-left: 5%;
}

.baglist-item-summary {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  margin-top: 10px;
  gap: 10px
}

.baglist {
  width: 100%;
  margin: 0 auto;
  color: #999;
  font-size: 14px;
}

.baglist-flex-details {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.baglist-flex-side {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-top: 40px;
}

.baglist-qty {
  display: flex;
  gap: 30px;
  align-items: center;
  background-color: #d8d8d8;
  padding: 2px;
}

.baglist-qty-price {
  margin-bottom: 0px;
  font-size: 14px;
  color: #000000;
}

.button-qty {
  border: none;
  width: 40px;
  padding: 2px;
}


.baglist-title {
  line-height: initial;
  font-size: 15px;
  display: block;
  margin-top: 5px;
  padding-top: 5px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
  color: black;
}

.cart-product-detail-price {
  font-size: 20px;
  color: #313131;
  font-weight: bold;
  margin-bottom: 0px;
}

.delete-button {
  text-align: start;
  font-weight: bold;
  color: var(--clr-black);
  cursor: pointer;
  transition: all 300ms;
}

.delete-button:hover {
  transform: scale(1.1);
}

.cart-product-detail-container {
  gap: 5px;
}

.order-summary {
  display: inline-block;
  vertical-align: top;
  width: 345px;
  background-color: #fff;
  padding: 20px;
  margin-top: 34px;
}

.order-details-total {
  margin: 0px;
  padding: 0px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

li {
  display: flex;
  justify-content: space-between;
  list-style: none;
  align-items: center;
}

.order-detail-right {
  color: #565656;
  line-height: initial;
  font-size: 15px;
  display: block;
  font-weight: bold;
}

.order-detail-total {
  font-size: 30px;
  color: #313131;
}

.cart-product-detail-text {
  font-size: 14px !important;
  color: #313131;
  margin: 0px;
}

.fwb {
  font-weight: bold;
  color: black;
}

.cart-product-detail-selection {
  margin-bottom: 0px;
}

/* /////////////////////// */

.modal-title {
  font-size: 30px;
}

.modal-flex {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

@media only screen and (max-width: 1500px) {
  .container-container {
    width: 100%;
    margin: 0px;
  }
}

@media only screen and (max-width: 1133px) {
  .container-container {
    width: 100%;
    margin: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .order-summary {
    align-items: flex-start;
  }

  .salvador {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .shopping-bag-content {
    background-color: var(--clr-lightGray);
    width: 80%;
    margin-right: 100px;
    margin: 0px;
    margin: 0 auto;
  }

  .bag-content {
    width: 100%;
    text-align: left;
    background: var(--clr-lightGray);
    position: relative;
    margin: 0px;
    /*     margin-left: 20px;
    margin-right: 20px;
    margin-top: 35px; */
  }

  .hr-before-wr {
    width: 100%;
    height: 2px;
    margin-left: 0px;
  }

  .shopping-bag-wrapper {
    width: 100%;
    height: 280px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    margin: 0px;
  }
}

@media only screen and (max-width: 772px) {

  .shopping-bag-content {
    background-color: var(--clr-lightGray);
    width: 100%;
    margin-right: 0px;
    margin: 0px;
    margin: 0 auto;
  }

  .hr-before-wr {
    width: 100%;
    height: 1px;
    margin-left: 0px;
  }

  .bag-content{
    width: 100%;
    height: 320px;
    text-align: left;
    background: var(--clr-lightGray);
    position: relative;
    margin: 0px;
    margin-bottom: 20px;
  }

  .shopping-bag-wrapper {
    width: 100%;
    height: 280px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    margin: 0px;
  }
  
  .shopping-bag-img {
    width: 120px;
    height: 110px;
    margin-right: 30px;
    aspect-ratio: 3/2;
    object-fit: contain;
    mix-blend-mode: darken;
  }
  
  .baglist-flex-side {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .baglist-qty {
    display: flex;
    gap: 10px;
    align-items: center;
    background-color: #d8d8d8;
    padding: 1px;
  }

  .baglist-qty-price {
    margin-bottom: 0px;
    font-size: 11px;
    color: #000000;
  }

  .button-qty {
    border: none;
    width: 40px;
    padding: 2px;
  }

  .cart-product-detail-price {
    font-size: 11px;
    color: #313131;
    font-weight: bold;
    margin-bottom: 0px;
    position: absolute;
    top: 98.7%;
    left: 8%;
  }

  .baglist-title {
    line-height: initial;
    font-size: 12px;
    display: block;
    margin-top: 5px;
    padding-top: 5px;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
    color: black;
    position: absolute;
    top: 10%;
    left: 0;
  }
}